import React, { useState, useRef } from 'react';
import { MoonStars, Sun, Trash, Download } from 'tabler-icons-react';
import { saveAs } from 'file-saver';
import {
  Button,
  Container,
  Text,
  Title,
  Modal,
  Select,
  Input,
  Group,
  Card,
  ActionIcon,
} from '@mantine/core';
import { MantineProvider, ColorSchemeProvider } from '@mantine/core';
import { useColorScheme } from '@mantine/hooks';
import { useHotkeys, useLocalStorage } from '@mantine/hooks';

export default function App() {
  const [tasks, setTasks] = useState([]);
  const [opened, setOpened] = useState(false);
  const [selectedExecutor, setSelectedExecutor] = useState('');
  const [selectedPriority, setSelectedPriority] = useState('');
  const [taskDescription, setTaskDescription] = useState('');
  const [startTime, setStartTime] = useState({});
  const [endTime, setEndTime] = useState({});
  const [intervalIds, setIntervalIds] = useState({});
  const executorOptions = ['Pracownik#1', 'Pracownik#2', 'Pracownik#3', 'Pracownik#4'];
  const priorityOptions = ['Wany', 'Bardzo wany', 'Pilny'];

  const preferredColorScheme = useColorScheme();
  const [colorScheme, setColorScheme] = useLocalStorage({
    key: 'mantine-color-scheme',
    defaultValue: 'light',
    getInitialValueInEffect: true,
  });
  const toggleColorScheme = () =>
    setColorScheme(colorScheme === 'dark' ? 'light' : 'dark');

  useHotkeys([['mod+J', toggleColorScheme]]);

  const taskTitle = useRef('');
  const personName = useRef('');
  const personSurname = useRef('');
  const personEmail = useRef('');

  const createTask = () => {
    const newTask = {
      tytul: taskTitle.current.value,
      opis: taskDescription,
      wykonawca: selectedExecutor,
      klient: {
        imie: personName.current.value,
        nazwisko: personSurname.current.value,
        email: personEmail.current.value,
      },
      priorytet: selectedPriority,
      dataDodania: new Date(),
      czasRozpoczecia: {},
      czasZakonczenia: {},
      czasTrwania: {},
    };
    setTasks([newTask, ...tasks]);
    setOpened(false);
    setTaskDescription('');
  };

  const deleteTask = index => {
    const updatedTasks = [...tasks];
    updatedTasks.splice(index, 1);
    setTasks(updatedTasks);
    const updatedStartTime = { ...startTime };
    delete updatedStartTime[index];
    setStartTime(updatedStartTime);
    const updatedEndTime = { ...endTime };
    delete updatedEndTime[index];
    setEndTime(updatedEndTime);
    const updatedIntervalIds = { ...intervalIds };
    clearInterval(updatedIntervalIds[index]);
    delete updatedIntervalIds[index];
    setIntervalIds(updatedIntervalIds);
  };

  const startTask = index => {
    const updatedStartTime = { ...startTime };
    updatedStartTime[index] = new Date();
    setStartTime(updatedStartTime);

    const updatedTasks = [...tasks];
    updatedTasks[index].czasRozpoczecia = new Date();
    setTasks(updatedTasks);

    const intervalId = setInterval(() => {
      const currentTime = new Date();
      const updatedTasks = [...tasks];
      updatedTasks[index].czasTrwania = calculateDuration(updatedStartTime[index], currentTime);
      setTasks(updatedTasks);
    }, 1000);

    const updatedIntervalIds = { ...intervalIds };
    updatedIntervalIds[index] = intervalId;
    setIntervalIds(updatedIntervalIds);
  };

  const calculateDuration = (start, end) => {
    const diff = Math.abs(end - start) / 1000;
    const hours = Math.floor(diff / 3600);
    const minutes = Math.floor((diff % 3600) / 60);
    const seconds = Math.floor(diff % 60);
    return { godziny: hours, minuty: minutes, sekundy: seconds };
  };

  const stopTask = index => {
    const updatedEndTime = { ...endTime };
    updatedEndTime[index] = new Date();
    setEndTime(updatedEndTime);

    clearInterval(intervalIds[index]);

    const updatedTasks = [...tasks];
    updatedTasks[index].czasZakonczenia = new Date();
    setTasks(updatedTasks);
  };

  const downloadReport = (task) => {
    const reportContent = `Imie: ${task.klient.imie}, 
Nazwisko: ${task.klient.nazwisko}, 
Temat: ${task.tytul}, 
Email: ${task.klient.email}, 
Opis: ${task.opis}, 
Wykonujcy: ${task.wykonawca}, 
Czas trwania: ${task.czasTrwania.godziny} godz. ${task.czasTrwania.minuty} min. ${task.czasTrwania.sekundy} sek.`;
    const blob = new Blob([reportContent], { type: 'text/plain' });
    saveAs(blob, `${task.tytul}_raport.txt`);
  };

  return (
    <ColorSchemeProvider
      colorScheme={colorScheme}
      toggleColorScheme={toggleColorScheme}>
      <MantineProvider
        theme={{ colorScheme, defaultRadius: 'md' }}
        withGlobalStyles
        withNormalizeCSS>
        <div className='App'>
          <Modal
            opened={opened}
            size={'md'}
            title={'Zadanie'}
            withCloseButton={false}
            onClose={() => {
              setOpened(false);
            }}
            centered
          >
            <Group mt={'md'}>
              <Input
                placeholder={'Imi'}
                ref={personName}
              />
              <Input
                placeholder={'Nazwisko'}
                ref={personSurname}
              />
              <Input
                ref={taskTitle}
                placeholder={'Temat'}
              />
              <Input
                placeholder={'Mail'}
                ref={personEmail}
              />
              <textarea
                value={taskDescription}
                onChange={(event) => setTaskDescription(event.target.value)}
                placeholder={'Opis zadania'}
                style={{
                  minHeight: '100px',
                  width: '100%',
                  borderRadius: '4px',
                  border: '1px solid #ccc',
                  padding: '8px',
                  marginTop: '8px'
                }}
              />
              <Select
                value={selectedExecutor}
                onChange={setSelectedExecutor}
                data={executorOptions}
                label="Wykonujcy"
                placeholder="Wybierz wykonujcego"
              />
              <Select
                value={selectedPriority}
                onChange={setSelectedPriority}
                data={priorityOptions}
                label="Priorytet"
                placeholder="Wybierz priorytet"
              />
            </Group>
            <Group mt={'md'} position={'apart'}>
              <Button
                onClick={() => {
                  setOpened(false);
                }}
                variant={'subtle'}
              >
                Anuluj
              </Button>
              <Button onClick={createTask}>Przydziel zadanie</Button>
            </Group>
          </Modal>
          <Container size={550} my={40}>
            <Group position={'apart'}>
              <Title>Zlecenia</Title>
              <ActionIcon
                color={'blue'}
                onClick={toggleColorScheme}
                size='lg'
              >
                {colorScheme === 'dark' ? (
                  <Sun size={16} />
                ) : (
                  <MoonStars size={16} />
                )}
              </ActionIcon>
            </Group>
            {tasks.length > 0 ? (
              tasks.map((task, index) => (
                <Card withBorder key={index} mt={'sm'}>
                  <Group position={'apart'}>
                    <Text weight={'bold'}>{task.tytul}</Text>
                    <ActionIcon
                      onClick={() => {
                        deleteTask(index);
                      }}
                      color={'red'}
                      variant={'transparent'}
                    >
                      <Trash />
                    </ActionIcon>
                    <Button
                      onClick={() => startTask(index)}
                      color={'green'}
                      variant={'outline'}
                    >
                      Rozpocznij
                    </Button>
                    <Button
                      onClick={() => stopTask(index)}
                      color={'blue'}
                      variant={'outline'}
                    >
                      Zakocz
                    </Button>
                    <ActionIcon
                      onClick={() => downloadReport(task)}
                      color={'blue'}
                      variant={'transparent'}
                    >
                      <Download />
                    </ActionIcon>
                  </Group>
                  <Text color={'dimmed'} size={'md'} mt={'sm'}>
                    {task.opis}
                  </Text>
                  <Text color={'dimmed'} size={'md'} mt={'sm'}>
                    {task.klient ? `Klient: ${task.klient.imie} ${task.klient.nazwisko}, Email: ${task.klient.email}` : ''}
                  </Text>
                  <Text color={'dimmed'} size={'md'} mt={'sm'}>
                    {task.wykonawca ? `Wykonawca: ${task.wykonawca}` : ''}
                  </Text>
                  <Text color={'dimmed'} size={'md'} mt={'sm'}>
                    {task.priorytet ? `Priorytet: ${task.priorytet}` : ''}
                  </Text>
                  <Text color={'dimmed'} size={'sm'} mt={'sm'}>
                    {task.dataDodania && `Data dodania: ${task.dataDodania.toLocaleString()}`}
                  </Text>
                  <Text color={'dimmed'} size={'sm'} mt={'sm'}>
                    {task.czasTrwania && `Czas trwania: ${task.czasTrwania.godziny} godz. ${task.czasTrwania.minuty} min.`}
                  </Text>
                </Card>
              ))
            ) : (
              <Text size={'lg'} mt={'md'} color={'dimmed'}>
                brak zlece
              </Text>
            )}
            <Button
              onClick={() => {
                setOpened(true);
              }}
              fullWidth
              mt={'md'}
            >
              Dodaj zlecenie
            </Button>
          </Container>
        </div>
      </MantineProvider>
    </ColorSchemeProvider>
  );
}
